* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

footer,
header,
aside {
  display: block;
}

html,
body {
  color: black;
  min-height: 100%;
  background: rgba(246, 246, 247, 1);
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  overflow-x: hidden;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: 'SF Pro Text';
  src: url('../font/SF\ Pro\ Text/SFProText-Regular.ttf');
}

@font-face {
  font-family: 'SF Pro Display';
  src: url('../font/SF\ Pro\ Display/SF-Pro-Display-Bold.otf');
}

@font-face {
  font-family: 'SF Pro Text Bold';
  src: url('../font/SF\ Pro\ Text/SFProText-Bold.ttf');
}

@font-face {
  font-family: 'SF Pro Text Medium';
  src: url('../font/SF\ Pro\ Text/SFProText-Medium.ttf');
}

@font-face {
  font-family: 'SF Pro Text Semi-bold';
  src: url('../font/SF\ Pro\ Text/SFProText-Semibold.ttf');
}

@font-face {
  font-family: 'SF-Pro-Display-Semibold';
  src: url('../font/SF\ Pro\ Display/SF-Pro-Display-Semibold.otf');
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0px 15px;
}